<template>
  <div class="calendar-list">
    <div class="header">
      <h1 class="app-name"></h1>
      <div class="new-calendar">
        <el-button
          type="text"
          @click="$router.push({ path: '/cloud' })"
          size="small"
          icon="el-icon-user"
          >账户</el-button
        >
        <el-button
          type="primary"
          @click="newCalendar"
          size="small"
          icon="el-icon-circle-plus"
          v-if="loaded && calendars.length"
          >新增</el-button
        >
      </div>
    </div>
    <div class="calendars">
      <div
        class="calendar"
        v-for="calendar in calendars"
        :key="calendar._id"
        @click="$router.push('/calendar/' + calendar._id)"
      >
        <div class="calendar-left">
          <CalendarLogo :calendar-id="calendar._id"></CalendarLogo>
        </div>
        <div class="calendar-center">
          <div class="calendar-name">{{ calendar.name }}</div>
          <div class="calendar-desc">{{ calendar.desc }}</div>
          <div class="calendar-date-range">
            <el-tag size="small"
              >{{ calendar.startDay }} ~ {{ calendar.endDay || "不限" }}</el-tag
            >&nbsp;
            <el-tag size="small" type="success"
              >{{ calendar.dayCount }}天</el-tag
            >
          </div>
        </div>
        <div class="calendar-right">
          <i class="el-icon-arrow-right"></i>
        </div>
      </div>
    </div>
    <div class="empty-tip" v-if="loaded && !calendars.length">
      啥也没有，现在<el-link @click="newCalendar" type="primary">新增</el-link
      >一个吧
    </div>
    <el-dialog title="新增日历" :visible.sync="setCalendarDialog" width="350px">
      <el-form :model="settingCalendarForm">
        <el-form-item label="名称">
          <el-input
            v-model="settingCalendarForm.name"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="备注">
          <el-input
            v-model="settingCalendarForm.desc"
            autocomplete="off"
            type="textarea"
          ></el-input>
        </el-form-item>
        <el-form-item label="起始日期">
          <el-date-picker
            v-model="settingCalendarForm.startDay"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            placeholder="开始日期"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="结束日期">
          <el-date-picker
            v-model="settingCalendarForm.endDay"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            placeholder="结束日期"
          >
          </el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="setCalendarDialog = false">取 消</el-button>
        <el-button type="primary" @click="setCalendar">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import CalendarLogo from "./CalendarLogo"
import dayjs from "dayjs"
export default {
  name: "CalendarList",
  data() {
    return {
      calendars: [],
      setCalendarDialog: false,
      settingCalendarForm: {
        name: "",
        desc: "",
        startDay: "",
        endDay: "",
      },
      loaded: false,
    }
  },
  components: {
    CalendarLogo,
  },
  async created() {
    const loading = this.$loading()
    // 加载所有日历
    const { data: calendars } = await this.cloud
      .database()
      .collection("yx_calendar")
      .get()
    loading.close()
    this.calendars = calendars.map((calendar) => {
      calendar.dayCount = dayjs().diff(calendar.startDay, "day") + 1
      return calendar
    })
    this.loaded = true
  },
  methods: {
    async setCalendar() {
      // 创建
      const { id: newCalendarId } = await this.cloud
        .database()
        .collection("yx_calendar")
        .add({
          ...this.settingCalendarForm,
          createTime: Date.now(),
        })
      // 查询刚才创建的日历放到列表后面即可
      const {
        data: [newCalendar],
      } = await this.cloud
        .database()
        .collection("yx_calendar")
        .doc(newCalendarId)
        .get()
      this.calendars.push(newCalendar)
      this.setCalendarDialog = false
    },
    newCalendar() {
      this.settingCalendarForm = {
        name: "新建日历",
        desc: "这是一段关于此日历的描述（非必填）",
        startDay: "2022-01-01",
        endDay: "",
      }
      this.setCalendarDialog = true
    },
  },
}
</script>
