<template>
  <div class="calendar-item" v-if="calendar">
    <div class="calendar-name-container">
      <el-page-header
        @back="$router.push('/')"
        :content="calendar.name"
        :title="accessByToken ? '管理（需登录）' : '首页'"
      >
      </el-page-header>
      <div class="delete-calendar" v-if="!accessByToken">
        <el-button
          type="default"
          size="small"
          icon="el-icon-edit"
          slot="reference"
          @click="editCalendar"
          >编辑</el-button
        >
      </div>
    </div>
    <div class="days">
      <!--上一页-->
      <div class="day-container" v-if="pageInfo.hasPre">
        <div class="day" @click="prePage"><div class="day-blank">P</div></div>
      </div>
      <div
        class="day-container"
        v-for="coloredDay in pagedColoredDays"
        :key="coloredDay.primaryDay.day"
      >
        <div
          @click="
            touchDay(coloredDay.primaryDay.day, coloredDay.primaryDay.dayNum)
          "
          class="day"
          :class="{
            'day-today': coloredDay.primaryDay.day == today.day,
            'day-weekend':
              coloredDay.primaryDay.week == 6 ||
              coloredDay.primaryDay.week == 0,
          }"
          :style="{
            background:
              (coloredDay.calendarDay && coloredDay.calendarDay.color) ||
              '#fff',
          }"
        >
          <div
            class="day-content"
            v-if="coloredDay.calendarDay"
            :style="{
              color: coloredDay.calendarDay.fontColor || '#000',
            }"
          >
            <i
              v-if="coloredDay.calendarDay.icon"
              class="icon-content"
              :class="'el-icon-' + coloredDay.calendarDay.icon"
            ></i>
            <span v-else>{{ coloredDay.calendarDay.content }}</span>
          </div>
          <div class="day-blank" v-else>
            {{
              coloredDay.primaryDay.day == today.day
                ? "今"
                : showDaysPageSetting.skip + coloredDay.primaryDay.dayNum
            }}
          </div>
        </div>
      </div>
      <!--下一页-->
      <div class="day-container" v-if="pageInfo.hasNext">
        <div class="day" @click="nextPage"><div class="day-blank">N</div></div>
      </div>
    </div>
    <div class="calendar-desc">{{ calendar.desc }}</div>
    <div class="calendar-desc">
      <span v-if="calendar.startDay">从{{ calendar.startDay }}</span
      ><span v-if="calendar.endDay">至{{ calendar.endDay }}</span>
    </div>
    <el-card class="box-card" v-if="!accessByToken">
      <div slot="header" class="clearfix">
        <span>插件：宝贝喝水<span class="not-importent">(beta)</span></span>
        <el-button
          style="float: right; padding: 3px 0"
          type="text"
          @click="editBabyDrinkWater"
          >编辑</el-button
        >
      </div>
      <div class="baby-drink-water" v-if="calendar.babyDrinkWater">
        <div class="baby-drink-water-config">
          <div class="from">{{ calendar.babyDrinkWater.from }}</div>
          <div class="from-to"><i class="el-icon-right"></i></div>
          <div class="to">
            {{ calendar.babyDrinkWater.to
            }}<span class="not-importent"
              >({{ calendar.babyDrinkWater.toEmail }})</span
            >
          </div>
        </div>
      </div>
      <div v-else>未启用</div>
    </el-card>
    <div v-if="!accessByToken" style="text-align:center;margin-top: 20px;">
      <el-popconfirm
        title="删除后不可恢复，确定删除吗？"
        @confirm="deleteCalendar"
      >
        <el-button
          type="danger"
          size="small"
          icon="el-icon-delete-solid"
          slot="reference"
          >删除</el-button
        >
      </el-popconfirm>
    </div>
    <el-dialog
      :title="'第' + settingCalendarDayForm.dayNum + '天'"
      :visible.sync="setCalendarDayDialog"
      width="350px"
    >
      <el-form :model="settingCalendarDayForm">
        <el-form-item label="日历日期">
          <div>{{ settingCalendarDayForm.day }}</div>
        </el-form-item>
        <el-form-item label="">
          <div>
            <el-button
              :style="{
                background: settingCalendarDayForm.color || 'rgb(0,0,0,0)',
              }"
              type="primary"
              :icon="'el-icon-' + settingCalendarDayForm.icon"
              @click="setIcon"
              >状态</el-button
            >
            <el-button
              type="text"
              icon="el-icon-delete"
              @click="settingCalendarDayForm.icon = ''"
              v-show="settingCalendarDayForm.icon"
              >清除符号</el-button
            >
          </div>
        </el-form-item>
        <el-form-item label="内容">
          <el-input
            v-model="settingCalendarDayForm.content"
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 50 }"
            placeholder="说点什么..."
            maxlength="1500"
            autofocus
          ></el-input>
        </el-form-item>
        <!-- <el-form-item label="背景色">
          <div class="selectable-colors">
            <div
              class="selectable-color"
              :class="{
                'selectable-color-active':
                  settingCalendarDayForm.color == selectableColor.color,
              }"
              v-for="selectableColor in selectableColors"
              :key="selectableColor.color"
              :style="{ background: selectableColor.color }"
              @click="settingCalendarDayForm.color = selectableColor.color"
            >
              <div
                class="selectable-color-example-content"
                :style="{ color: settingCalendarDayForm.fontColor }"
              >
                {{ selectableColor.type }}
              </div>
            </div>
            <div class="selectable-color">
              <el-color-picker
                v-model="settingCalendarDayForm.color"
              ></el-color-picker>
            </div>
          </div>
        </el-form-item> -->
        <!-- <el-form-item label=" ">
          <el-switch
            v-model="settingCalendarDayForm.fontColor"
            active-text="白色字体"
            inactive-text="黑色字体"
            active-value="#fff"
            inactive-value="#000"
          >
          </el-switch>
        </el-form-item> -->
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="setCalendarDayDialog = false">取 消</el-button>
        <el-button type="primary" @click="setCalendarDay">就这样</el-button>
      </div>
    </el-dialog>
    <el-dialog title="我的状态" :visible.sync="setIconDialog" width="350px">
      <div class="icons">
        <div
          class="icon"
          :style="{ background: randomColor, color: calcedFontColor }"
          @click="touchIcon(icon)"
          v-for="icon in icons"
          :key="icon"
        >
          <div class="icon-c">
            <i :class="'el-icon-' + icon"></i>
          </div>
        </div>
      </div>
    </el-dialog>
    <el-dialog
      title="宝贝喝水"
      :visible.sync="showBabyDrinkWaterDialog"
      width="350px"
    >
      <el-form :model="babyDrinkWater" v-if="babyDrinkWater">
        <el-form-item label="发信人署名">
          <el-input
            v-model="babyDrinkWater.from"
            placeholder="请输入署名"
            type="text"
          ></el-input>
        </el-form-item>
        <el-form-item label="收信人称谓">
          <el-input
            v-model="babyDrinkWater.to"
            type="text"
            placeholder="请输入称谓"
          ></el-input>
        </el-form-item>
        <el-form-item label="收信人邮箱">
          <el-input v-model="babyDrinkWater.toEmail" type="text"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="removeBabyDrinkWater">删除</el-button>
        <el-button type="primary" @click="saveBabyDrinkWater">保存</el-button>
      </div>
    </el-dialog>
    <el-dialog
      title="编辑日历"
      :visible.sync="editCalendarDialog"
      width="350px"
    >
      <CalendarItemForm
        :calendarItem="editingCalendarItem"
        v-if="editingCalendarItem"
      ></CalendarItemForm>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancleEdit">取 消</el-button>
        <el-button type="primary" @click="saveEditCalendar">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import CalendarItemForm from "./CalendarItemForm.vue"
export default {
  name: "CalendarItem",
  props: {
    id: String,
  },
  components: { CalendarItemForm },
  data() {
    return {
      editCalendarDialog: false,
      editingCalendarItem: null,
      calendar: null,
      babyDrinkWater: null, // 宝贝喝水插件
      calendarDays: [],
      setCalendarDayDialog: false,
      settingCalendarDayForm: {},
      showDaysPageSetting: {
        skip: 0,
        limit: 1000,
      },
      today: {},
      adviceColors: ["#ff9999", "#99ff66", "#ff99ff", "#66ffff"],
      recentColors: [],
      setIconDialog: false,
      selectedIcon: "",
      calcedFontColor: "",
      randomColor: "",
      icons: [
        "camera",
        "s-home",
        "s-opportunity",
        "sunny",
        "cloudy",
        "moon",
        "chicken",
        "sugar",
        "watermelon",
        "grape",
        "lollipop",
        "apple",
        "refrigerator",
        "tableware",
        "coffee-cup",
        "wind-power",
      ],
      showBabyDrinkWaterDialog: false,
    }
  },
  async created() {
    // 加载最近使用的颜色
    this.recentColors = JSON.parse(localStorage.getItem("recentColors") || "[]")
    // 今天
    this.today = this.toDayObj(new Date())
    const loading = this.$loading()
    // 加载日历&日志
    if (this.accessByToken) {
      // by accessToken
      this.calendar = await this.loadCalendarByToken()
      this.calendarDays = await this.loadCalendarDayByToken()
    } else {
      const {
        data: [calendar],
      } = await this.cloud
        .database()
        .collection("yx_calendar")
        .doc(this.id)
        .get()
      this.calendar = calendar
      // 加载日子
      const { data: calendarDays } = await this.cloud
        .database()
        .collection("yx_calendar_day")
        .where({
          calendarId: this.id,
        })
        .get()
      this.calendarDays = calendarDays
    }
    loading.close()
    // TODO 能够给用户机会加载所有的记录
    if (this.calendarDays.length == 100) {
      this.$notify({
        title: "提示",
        message: "超过100条的记录未被加载",
        type: "waning",
      })
    }
  },
  computed: {
    /**
     * 算法，根据calendar.startDate & calendar.endDate计算范围内的所有days
     */
    primaryDays() {
      const days = []
      const endDate = new Date(this.calendarEndDay)
      let trackDate = new Date(this.calendar.startDay)
      let dayNum = 1
      do {
        days.unshift({
          ...this.toDayObj(trackDate),
          dayNum: dayNum++,
        })
        trackDate.setDate(trackDate.getDate() + 1)
      } while (trackDate <= endDate)
      return days
    },
    coloredDays() {
      return this.primaryDays.map((primaryDay) => {
        const calendarDay = this.calendarDays.find((calendarDay) => {
          return calendarDay.day == primaryDay.day
        })
        return {
          primaryDay,
          calendarDay: calendarDay,
        }
      })
    },
    pagedColoredDays() {
      return this.coloredDays.slice(
        this.showDaysPageSetting.skip,
        this.showDaysPageSetting.skip + this.showDaysPageSetting.limit
      )
    },
    pageInfo() {
      return {
        hasPre:
          this.pagedColoredDays[this.pagedColoredDays.length - 1].primaryDay
            .day != this.calendar.startDay,
        hasNext: this.pagedColoredDays[0].primaryDay.day != this.calendarEndDay,
      }
    },
    calendarEndDay() {
      return this.calendar.endDay || this.today.day
    },
    selectableColors() {
      return this.recentColors
        .map((color) => {
          return {
            type: "最近",
            color,
          }
        })
        .concat(
          this.adviceColors.map((color) => {
            return {
              type: "建议",
              color,
            }
          })
        )
    },
    accessByToken() {
      return !!this.$route.query.accessToken
    },
  },
  watch: {
    "settingCalendarDayForm.color": function(recentColor) {
      // 暂存最近使用的颜色
      if (
        recentColor &&
        !this.adviceColors.includes(recentColor) &&
        !this.recentColors.includes(recentColor)
      ) {
        this.recentColors = [
          recentColor,
          ...this.recentColors.filter((item) => {
            return item != recentColor
          }),
        ].slice(0, 5)
      }
    },
  },
  methods: {
    editCalendar() {
      this.editCalendarDialog = true
      this.editingCalendarItem = JSON.parse(JSON.stringify(this.calendar))
    },
    cancleEdit() {
      this.editCalendarDialog = false
      this.editingCalendarItem = null
    },
    async saveEditCalendar() {
      console.log(this.editingCalendarItem)
      const update = {
        ...this.editingCalendarItem,
      }
      delete update._id
      delete update._openid
      const res = await this.cloud
        .database()
        .collection("yx_calendar")
        .doc(this.editingCalendarItem._id)
        .update(update)
        .catch((err) => {
          console.error(err)
        })
      console.log(res)
      this.calendar = this.editingCalendarItem
      this.cancleEdit()
    },
    // 删除宝贝喝水配置
    async removeBabyDrinkWater() {
      await this.cloud
        .database()
        .collection("yx_calendar")
        .doc(this.id)
        .update({
          babyDrinkWater: this.cloud.database().command.remove(),
        })
      this.calendar.babyDrinkWater = this.babyDrinkWater = null
      this.showBabyDrinkWaterDialog = false
    },
    // 保存宝贝喝水配置
    async saveBabyDrinkWater() {
      await this.cloud
        .database()
        .collection("yx_calendar")
        .doc(this.id)
        .update({
          babyDrinkWater: this.babyDrinkWater,
        })
      this.calendar.babyDrinkWater = this.babyDrinkWater
      this.showBabyDrinkWaterDialog = false
    },
    // 编辑宝贝喝水配置
    editBabyDrinkWater() {
      this.babyDrinkWater = JSON.parse(
        JSON.stringify(
          this.calendar.babyDrinkWater || {
            from: "",
            to: "",
            toEmail: "",
          }
        )
      )
      this.showBabyDrinkWaterDialog = true
    },
    toDayObj: function(date) {
      var year = date.getFullYear()
      var month = date.getMonth() + 1
      var dayOfMonth = date.getDate()
      var week = date.getDay()

      var monthMM = "" + month
      if (month < 10) {
        monthMM = "0" + month
      }
      var dayDD = "" + dayOfMonth
      if (dayOfMonth < 10) {
        dayDD = "0" + dayOfMonth
      }
      return {
        day: "" + year + "-" + monthMM + "-" + dayDD,
        year,
        month,
        dayOfMonth,
        week,
      }
    },
    // 设置封面图标
    setIcon() {
      // 随机背景色
      const colorR = Math.floor(Math.random() * 256)
      const colorG = Math.floor(Math.random() * 256)
      const colorB = Math.floor(Math.random() * 256)
      this.randomColor = `rgb(${colorR}, ${colorG}, ${colorB})`
      // 根据随机背景色自动选择白色文字或黑色文字
      this.calcedFontColor = colorR + colorG + colorB < 370 ? "#fff" : "#000"
      this.setIconDialog = true
    },
    touchIcon(icon) {
      this.settingCalendarDayForm.icon = icon
      this.settingCalendarDayForm.color = this.randomColor
      this.setIconDialog = false
    },
    touchDay(day, dayNum) {
      // 如果已经编辑过，那么使用消息弹出，否则显示编辑dialog
      const calendarDay = this.calendarDays.find((calendarDay) => {
        return calendarDay.day == day
      })
      this.showSettingCalendarDayForm(day, dayNum, calendarDay)
    },
    showSettingCalendarDayForm(day, dayNum, existsCalendarDay) {
      this.settingCalendarDayForm = {
        day,
        dayNum,
        content: "",
        icon: this.selectedIcon || "",
        color:
          this.randomColor ||
          this.recentColors[0] ||
          this.adviceColors[0] ||
          "",
        fontColor: this.calcedFontColor || "#000",
      }
      if (existsCalendarDay) {
        this.settingCalendarDayForm.content = existsCalendarDay.content
        this.settingCalendarDayForm.color = existsCalendarDay.color
        this.settingCalendarDayForm.fontColor = existsCalendarDay.fontColor
        this.settingCalendarDayForm.icon = existsCalendarDay.icon
      }
      this.setCalendarDayDialog = true
    },
    async setCalendarDay() {
      // 如果已经存在，需要先删除
      const exitsCalendarDayIndex = this.calendarDays.findIndex(
        (calendarDay) => {
          return calendarDay.day == this.settingCalendarDayForm.day
        }
      )
      let newCalendarDay
      if (this.accessByToken) {
        newCalendarDay = await this.setCalendarDayByToken()
      } else {
        if (exitsCalendarDayIndex >= 0) {
          await this.cloud
            .database()
            .collection("yx_calendar_day")
            .doc(this.calendarDays[exitsCalendarDayIndex]._id)
            .remove()
        }
        // 新增
        const { id: newCalendarDayId } = await this.cloud
          .database()
          .collection("yx_calendar_day")
          .add({
            calendarId: this.calendar._id,
            ...this.settingCalendarDayForm,
            createTime: Date.now(),
          })
        // 查询刚才创建的日子放到列表后面即可
        const { data } = await this.cloud
          .database()
          .collection("yx_calendar_day")
          .doc(newCalendarDayId)
          .get()
        newCalendarDay = data[0]
      }
      // 本地删除
      if (exitsCalendarDayIndex >= 0) {
        this.calendarDays.splice(exitsCalendarDayIndex, 1)
      }
      console.log(newCalendarDay)
      this.calendarDays.push(newCalendarDay)
      this.setCalendarDayDialog = false
      this.setIconDialog = false
      this.selectedIcon = ""
    },
    prePage() {
      this.showDaysPageSetting.skip -= this.showDaysPageSetting.limit
    },
    nextPage() {
      this.showDaysPageSetting.skip += this.showDaysPageSetting.limit
    },
    async deleteCalendar() {
      await this.cloud
        .database()
        .collection("yx_calendar")
        .doc(this.calendar._id)
        .remove()
      this.$notify({
        title: "成功",
        message: "已成功删除此日历",
        type: "success",
      })
      this.$router.push("/index")
    },
    loadCalendarByToken() {
      return fetch(
        "https://service-i146fyvj-1252108641.sh.apigw.tencentcs.com/release/baby-drink-water",
        {
          method: "POST",
          body: JSON.stringify({
            action: "yx-get-calendar",
            data: {
              ...this.$route.query,
              calendarId: this.id,
            },
          }),
        }
      )
        .then((res) => res.json())
        .then((data) => data.calendar)
    },
    loadCalendarDayByToken() {
      return fetch(
        "https://service-i146fyvj-1252108641.sh.apigw.tencentcs.com/release/baby-drink-water",
        {
          method: "POST",
          body: JSON.stringify({
            action: "yx-query-calendar-day",
            data: {
              ...this.$route.query,
              calendarId: this.id,
            },
          }),
        }
      )
        .then((res) => res.json())
        .then((data) => data.calendarDays)
    },
    setCalendarDayByToken() {
      return fetch(
        "https://service-i146fyvj-1252108641.sh.apigw.tencentcs.com/release/baby-drink-water",
        {
          method: "POST",
          body: JSON.stringify({
            action: "yx-set-calendar-day",
            data: {
              ...this.$route.query,
              calendarId: this.id,
              calendarDay: {
                ...this.settingCalendarDayForm,
              },
            },
          }),
        }
      )
        .then((res) => res.json())
        .then((data) => data.newCalendarDay)
    },
  },
  beforeDestroy() {
    // 保存最近使用的颜色
    localStorage.setItem("recentColors", JSON.stringify(this.recentColors))
  },
}
</script>

