<template>
  <div class="calendar-logo">
    <div
      class="calendar-day"
      v-for="logoCalendarDay in logoCalendarDays"
      :key="logoCalendarDay._id"
      :style="{
        background: logoCalendarDay.color,
        color: logoCalendarDay.fontColor,
      }"
    >
      <div class="calendar-day-content">
        {{ logoCalendarDay.contentFirstChar }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "CalendarLogo",
  props: {
    calendarId: String,
  },
  data() {
    return {
      calendarDays: [],
    };
  },
  async created() {
    // 加载日子
    const { data: calendarDays } = await this.cloud
      .database()
      .collection("yx_calendar_day")
      .where({
        calendarId: this.calendarId,
      })
      .limit(9)
      .get();
    this.calendarDays = calendarDays;
  },
  computed: {
    logoCalendarDays() {
      const logoCalendarDays = [...this.calendarDays];
      while (logoCalendarDays.length < 9) {
        logoCalendarDays.push({
          _id: "c" + logoCalendarDays.length,
          content: "",
          color: "#aaa",
          fontColor: "#000",
        });
      }
      return logoCalendarDays.map((calendarDay) => {
        return {
          ...calendarDay,
          contentFirstChar: calendarDay.content.substr(0, 1),
        };
      });
    },
  },
};
</script>

<style scoped>
.calendar-logo {
  padding: 5px;
  width: 70px;
  height: 70px;
  background: #ddd;
  border-radius: 10px;
  margin-right: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 5px;
}
.calendar-day {
  background: #333;
  height: 20px;
  border-radius: 5px;
  display: flex;
}
.calendar-day-content {
  margin: auto;
  font-size: 11px;
}
</style>
