import Vue from "vue";
import App from "./App.vue";
import VueRouter from "vue-router";
import cloudbase from "@cloudbase/js-sdk";
import CalendarItem from "./components/CalendarItem.vue";
import CalendarList from "./components/CalendarList.vue";
import EoCloud from "./components/EoCloud.vue";
import ElementUI from "element-ui";
import "element-ui/lib/theme-chalk/index.css";

// 链接云服务
const cloud = Vue.prototype.cloud = cloudbase.init({
  env: process.env.VUE_APP_CLOUD_NAME,
});
const cloudAuth = Vue.prototype.cloudAuth = cloud.auth({
  persistence: "local",
});

// 应用路由插件
Vue.use(VueRouter);

Vue.use(ElementUI);

Vue.config.productionTip = false;

const routes = [
  { path: "/", redirect: "/index" },
  { path: "/index", component: CalendarList },
  { path: "/calendar/:id", component: CalendarItem, props: true },
  { path: "/cloud", component: EoCloud, props: true },
];

const router = new VueRouter({
  routes, // （缩写）相当于 routes: routes
});

// 路由守卫，登录验证
router.beforeEach((to, from, next) => {
  if (to.path !== '/cloud' && !cloudAuth.hasLoginState() && !to.query.accessToken) {
    next('/cloud')
  }
  else {
    next()
  }
})

new Vue({
  render: (h) => h(App),
  router,
}).$mount("#app");